@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
  font-size: 16px;
}

.App{
  width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #FFE6E6;
}

.container{
  width: 40%;

}

.wrapper{
  width: 100%;
  padding: 2rem 1rem 1rem;
  margin: 1rem 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #C6DCE4;
  border-radius: 1rem;
}

#logo{
  width: 70%;
}

#filename{
  padding-left: 0.5rem;
}

#file{
  display: none;
}

form{
  width: 100%;
  padding: 2rem 1rem 0;
}

.file-control{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

#file-label{
  display: flex;
  background-color: #8CC0DE;
  height: 2.5rem;
  width: 30%;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 5px;
  transition: all ease-in 0.2s;
  font-weight: 500;
}

#file-label:hover{
  background-color: #a2c5da;
}

.file-control input{
  height: 2.5rem;
  width: 67%;
  border: none;
  background-color:#F2F2F2;
  border-radius: 5px;
}

.submit{
  width: 100%;
  height: 2.5rem;
  margin: 1rem 0;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  background-color: #FFD9C0;
  cursor: pointer;
  transition: all ease-in 0.2s;
}

.submit:hover{
  background-color: #a9cee4;
}

.credit{
  font-weight: 300;
  font-size: 0.8rem;
}

.card{
  width: 100%;
  padding: 1rem;
  margin: 1rem 0;
  background-color: #FAF0D7;
  border-radius: 10px;
}

.title{
  font-size: 1rem;
  margin-bottom: 0.7rem;
}

.status-file, .status-file a{
  font-size: 0.8rem;
  font-weight: 300;
}




@media only screen and (max-width: 768px) {
  *{
    font-size: 14px;
  }

  .container{
    width: 90%;
  }
}